"use client";
import { useEffect, useState } from "react";

interface Business {
  _id: string;
  name: string;
  category: string;
  description: string;
}

export default function Landing() {
  const [businesses, setBusinesses] = useState<Business[]>([]);

  useEffect(() => {
    const fetchBusinesses = async () => {
      try {
        const res = await fetch("http://localhost:5000/api/business/viewBizs");
        const data = await res.json();
        setBusinesses(data.businesses);
      } catch (err) {
        console.error("Failed to fetch businesses:", err);
      }
    };

    fetchBusinesses();
  }, []);

  return (
    <main className="p-6">
      <h1 className="text-2xl font-bold mb-4">Business Directory</h1>
      <div className="space-y-4">
        {businesses.map((biz) => (
          <div key={biz._id} className="border rounded-lg p-4 shadow-md">
            <h2 className="text-xl font-semibold">{biz.name}</h2>
            <p className="text-sm text-gray-500">{biz.category}</p>
            <p className="text-gray-700">{biz.description}</p>
          </div>
        ))}
      </div>
    </main>
  );
}
